export default {
	data() {
		return {
			items: []
		};
	},
	onShow() {
		this.selectAds();
	},
	methods: {
		toModify(id) {
			uni.navigateTo({
				url: `/pages/ad/addAd/addAd?id=${id}&type=modify`
			});
		},
		async toDelete(id) {
			uni.showModal({
				title: '删除',
				content: '确定删除？',
				success: async res => {
					if (res.confirm) {
						const res = await this.$util.request({
							requestUrl: 'api/ad/' + id,
							method: 'DELETE'
						});
						console.log('删除广告', res);

						if (res !== undefined) {
							this.$util.toast('删除成功');
							this.selectAds();
						}
					}
				}
			});
		},
		async selectAds() {
			const res = await this.$util.request({
				requestUrl: 'api/ads'
			});
			console.log('查询所有广告：', res);

			if (res !== undefined) {
				this.items = res.data.data;
			}
		}
	}
};
